.ButtonFieldWrapper {
    display: -ms-flexbox;
    display: flex;
    margin-top: 19px;

}

    .ButtonFieldWrapper_center {
        -ms-flex-pack: center;
            justify-content: center;
    }

    .ButtonFieldWrapper_right {
        -ms-flex-pack: end;
            justify-content: flex-end;
    }

    .ButtonFieldWrapper :global .Button__button {
        width: 45%;
        height: 67px;
        padding-left: 0;
        padding-right: 0;
        text-align: center;
    }

    .ButtonFieldWrapper :global .Button__button span {
            line-height: 26px;
            font-size: 26px;
        }



